import React, { useState, useEffect } from 'react';
import './UsersTable.css'
import Table from 'react-bootstrap/Table'
import axios from 'axios';
import { Link } from 'react-router-dom';

const UsersTable = () => {

  const [users, setUsers] = useState([]);

  useEffect(() => {

    const getAllUsers = async () => {
      const users = await axios.get('https://bank-fullstack-project.herokuapp.com/api/bank');
      setUsers(users.data);
    }

    getAllUsers();

  }, [])

  return (
    <>
      <div className="users-table">

        <Table striped bordered hover variant="dark">
          <thead>
            <tr>
              <th>#</th>
              <th>Email</th>
              <th>Cash</th>
              <th>Credit</th>
              <th>Active</th>
              <th>Actions</th>
            </tr>
          </thead>
          <tbody>
            {users.map((user, index) => {
              return <tr key={index}>
                <td>{index + 1}</td>
                <td>{user.email}</td>
                <td>{user.cash}</td>
                <td>{user.credit}</td>
                <td>{user.isActive ? <i className="fas fa-user-check"></i> : <i className="fas fa-user-alt-slash"></i>}</td>
                <td className="action"><Link to={{ pathname: `/user-actions/${user._id}`, user }}><i className="fas fa-external-link-alt"></i></Link></td>
              </tr>
            })}
          </tbody>
        </Table>
      </div>

    </>
  )
}

export default UsersTable;