import React, { useState } from 'react';
import UsersTable from '../../components/usersTable/UsersTable.component';
import './Main.css';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import Form from 'react-bootstrap/Form';
import axios from 'axios';

const Main = () => {

    const [show, setShow] = useState(false);
    const [user, setUser] = useState({ email: '', cash: 0, credit: 0, isActive: false });

    const handleShow = (e) => {
        setShow(true);
    }
    const handleClose = () => {
        setShow(false);
    }

    const createUser = async () => {
        try {
            const response = await axios.post('https://bank-fullstack-project.herokuapp.com/api/bank', user);
            console.log(response.data);
        } catch (err) {
            console.log(err);
        }
        setUser({ email: '', cash: 0, credit: 0, isActive: false });
        handleClose();
    }


    return (
        <>
            <main className="main">
                <Navbar collapseOnSelect expand="lg" bg="light" variant="light">
                    <Nav className="mr-auto">
                        <Button variant="outline-info" onClick={handleShow}>Add New User</Button>
                    </Nav>
                </Navbar>

                <Modal show={show} onHide={handleClose} centered>
                    <Modal.Header closeButton>
                        <Modal.Title>Create New User</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>

                        <Form.Label>Email address :</Form.Label>
                        <Form.Control type="email" placeholder="Enter email" required onChange={(e) =>
                            setUser({ email: e.target.value, cash: user.cash, credit: user.credit, isActive: user.isActive })} />
                        <br />
                        <Form.Label>Cash :</Form.Label>
                        <Form.Control type="number" min="0" placeholder="Enter Amount Of Cash" onChange={(e) =>
                            setUser({ email: user.email, cash: e.target.value, credit: user.credit, isActive: user.isActive })} />
                        <br />
                        <Form.Label>Credit :</Form.Label>
                        <Form.Control type="number" min="0" placeholder="Enter Credit Number" onChange={(e) =>
                            setUser({ email: user.email, cash: user.cash, credit: e.target.value, isActive: user.isActive })} />
                        <br />
                        <Form.Check type="checkbox" label="Active" onChange={(e) =>
                            setUser({ email: user.email, cash: user.cash, credit: user.credit, isActive: e.target.checked })} />

                    </Modal.Body>
                    <Modal.Footer>
                        <Button variant="secondary" onClick={handleClose}>Close</Button>
                        <Button variant="primary" onClick={createUser}>Send</Button>
                    </Modal.Footer>
                </Modal>

                <div className="usersTable">
                    <UsersTable />
                </div>
            </main>
        </>
    )
}

export default Main;