import React, { useEffect, useState } from 'react';
import './UserActions.css';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import axios from 'axios';
import Table from 'react-bootstrap/Table';
import Modal from 'react-bootstrap/Modal';
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';

const UserActions = (user) => {

    const [userDetails] = useState(user.history.location.user);
    const [userHistory, setUserHistory] = useState([]);
    const [show, setShow] = useState(false);
    const [amount, setAmount] = useState(0);
    const [operation, setOperation] = useState('');
    const [transferModal, setTransferModal] = useState(false);
    const [recievingId, setRecievingId] = useState('');

    const handleClose = () => {
        setShow(false);
        setTransferModal(false);
    }
    const handleShow = (e) => {
        setOperation(e.target.innerHTML);
        setShow(true);
    }

    const handleTransferModalShow = () => {
        setTransferModal(true);
    }

    useEffect(() => {
        const getUserOperationsHistory = async (id) => {
            const history = await axios.get(`https://bank-fullstack-project.herokuapp.com/api/bank/getUserOperationHistory/${id}`);
            setUserHistory(history.data);
        }

        getUserOperationsHistory(userDetails._id);
    }, [userDetails._id, amount])

    const handleOperationModal = async () => {
        let response;
        if (operation === 'Withdraw') {
            response = await axios.put('https://bank-fullstack-project.herokuapp.com/api/bank/withdrawCash',
                { id: userDetails._id, cash: amount });
        }
        else if (operation === 'Deposite') {
            response = await axios.put('https://bank-fullstack-project.herokuapp.com/api/bank/deposite',
                { id: userDetails._id, cash: amount });
        }
        else if (operation === 'Update Credit') {
            response = await axios.put('https://bank-fullstack-project.herokuapp.com/api/bank/updateCredit',
                { id: userDetails._id, credit: amount });

        }
        console.log(response.data);
        setAmount(0);
        setShow(false);
    }

    const handleTransferModal = async () => {
        const response = await axios.put('https://bank-fullstack-project.herokuapp.com/api/bank/transferring',
        {receivingUserId: recievingId, sendingUserId: userDetails._id, amount});
        console.log(response.data);
        setAmount(0);
        setTransferModal(false);
    }

    return (
        <>
            <div className="userActionsContainer">
                <Navbar collapseOnSelect expand="lg" bg="dark" variant="dark">
                    <Nav className="mr-auto">
                        <Nav.Link onClick={handleShow}>Withdraw</Nav.Link>
                        <Nav.Link onClick={handleShow}>Deposite</Nav.Link>
                        <Nav.Link onClick={handleShow}>Update Credit</Nav.Link>
                        <Nav.Link onClick={handleTransferModalShow}>Transfer Money</Nav.Link>
                    </Nav>
                </Navbar>

                <div className="operations-table">
                    <Table striped bordered hover size="sm" className="oper-table">
                        <thead>
                            <tr>
                                <th>#</th>
                                <th>Type</th>
                                <th>Description</th>
                            </tr>
                        </thead>
                        <tbody>
                            {userHistory.map((operation, index) => {
                                return <tr key={index}>
                                    <td>{index + 1}</td>
                                    <td>{operation.operation_type}</td>
                                    <td>{operation.description}</td>
                                </tr>
                            })}
                        </tbody>
                    </Table>
                </div>

                <Modal show={show} onHide={handleClose} centered>
                    <Modal.Header closeButton>
                        <Modal.Title>{operation}</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <Form.Control type="number" value={amount} min="0" onChange={(e) => setAmount(e.target.value)} />
                    </Modal.Body>
                    <Modal.Footer>
                        <Button variant="secondary" onClick={handleClose}>Close</Button>
                        <Button variant="primary" onClick={handleOperationModal}>Send</Button>
                    </Modal.Footer>
                </Modal>

                <Modal show={transferModal} onHide={handleClose} centered>
                    <Modal.Header closeButton>
                        <Modal.Title>Transferr Money</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <Form.Control type="text" value={recievingId} placeholder="Reciving User Id" onChange={(e) => setRecievingId(e.target.value)} /> 
                        <br></br>
                        <Form.Control type="number" value={amount} min="0" placeholder="Amount" onChange={(e) => setAmount(e.target.value)} />
                    </Modal.Body>
                    <Modal.Footer>
                        <Button variant="secondary" onClick={handleClose}>Close</Button>
                        <Button variant="primary" onClick={handleTransferModal}>Send</Button>
                    </Modal.Footer>
                </Modal>

            </div>
        </>
    )
}

export default UserActions;